.hd-filter-box {
  margin: 20px; }
  .hd-filter-box .hd-filter-options {
    background: #fff;
    border-radius: 5px;
    overflow: hidden;
    transition: all 0.5s; }
    .hd-filter-box .hd-filter-options .hd-filter-option {
      padding: 10px 15px;
      border-bottom: 1px solid #f1f1f1;
      line-height: 26px; }
      .hd-filter-box .hd-filter-options .hd-filter-option .hd-filter-option-name {
        width: 80px;
        display: inline-block;
        vertical-align: top; }
      .hd-filter-box .hd-filter-options .hd-filter-option .hd-filter-option-lists {
        display: inline-block;
        width: calc(100% - 80px); }
        .hd-filter-box .hd-filter-options .hd-filter-option .hd-filter-option-lists .hd-filter-options-tag {
          display: inline-block;
          border: 1px dashed #0e64c7;
          padding: 0 5px 0 15px;
          border-radius: 4px;
          margin: 2px 15px 2px 0;
          line-height: 20px; }
        .hd-filter-box .hd-filter-options .hd-filter-option .hd-filter-option-lists .hd-filter-options-list {
          display: inline-block;
          cursor: pointer;
          margin: 2px 15px 2px 0; }
          .hd-filter-box .hd-filter-options .hd-filter-option .hd-filter-option-lists .hd-filter-options-list .hd-filter-option-selected {
            color: #0e64c7; }
    .hd-filter-box .hd-filter-options .hd-filter-tab-down {
      opacity: 1; }
    .hd-filter-box .hd-filter-options .hd-filter-tab-up {
      opacity: 0;
      height: 0; }
