.hd-card-wrap {
  display: inline-block;
  background: #fff;
  margin: 20px;
  padding: 16px;
  font-size: 14px;
  min-width: 200px; }
  .hd-card-wrap .hd-card-title {
    font-size: 14px;
    color: #808492;
    margin-bottom: 10px; }
  .hd-card-wrap .hd-card-value {
    font-size: 30px;
    color: #333333;
    font-weight: bold; }
  .hd-card-wrap .hd-card-content::before, .hd-card-wrap .hd-card-content::after {
    content: '';
    display: table; }
  .hd-card-wrap .hd-card-content::after {
    clear: both; }
  .hd-card-wrap .hd-card-content .hd-card-content-cell {
    font-size: 14px;
    color: #808492;
    margin-top: 5px;
    margin-right: 20px;
    display: inline-block; }
  .hd-card-wrap .hd-card-desc {
    margin-top: 5px; }
  .hd-card-wrap .block .hd-card-content-cell {
    display: block; }
