/* 选择下拉选中背景色 */
.select .ant-select-selection {
  border: 1px solid #128dcd;
  background: #1a3b6d; }

.select .ant-select-arrow {
  color: #fff; }

.select .ant-select-selection-selected-value {
  color: #fff; }

.select .select-label {
  margin-right: 12px;
  color: #b0bacb;
  font-size: 14px; }

.ant-select-dropdown {
  background-color: #1a3b6d !important; }

.ant-select-selection--multiple .ant-select-selection__choice {
  background: #2161b7 !important;
  color: #fff !important;
  border: none !important; }

.ant-select-selection--multiple .ant-select-selection__choice__remove {
  color: #fff !important; }

