.ranking {
  background: #092d49;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  position: relative;
  padding: 10px 0;
  height: 100%; }
  .ranking div {
    flex: 1; }
  .ranking .ranking-name {
    display: inline-block;
    width: 35%;
    text-align: right;
    margin-right: 8px;
    color: #fff; }
  .ranking .ranking-value {
    display: inline-block;
    text-align: left;
    margin-left: 8px;
    /* color: $verse-font-color; */
    color: #00f6f7; }
  .ranking .ranking-top {
    font-size: 14px;
    display: inline-block;
    width: 30%;
    text-align: left;
    float: right;
    font-size: 13px;
    color: #9198a7; }
  .ranking .ranking-top0 {
    color: #f53d4b; }
  .ranking .ranking-top1 {
    color: #f5d004; }
  .ranking .ranking-top2 {
    color: #37e275; }
  .ranking .corner-top-left {
    display: block;
    width: 12px;
    height: 12px;
    float: left;
    border-left: 2px solid #00ffff;
    border-top: 2px solid #00ffff;
    position: absolute;
    top: -1px;
    left: -1px; }
  .ranking .corner-top-right {
    display: block;
    width: 12px;
    height: 12px;
    float: left;
    border-top: 2px solid #00ffff;
    border-right: 2px solid #00ffff;
    position: absolute;
    top: -1px;
    right: -1px; }
  .ranking .corner-bottom-right {
    display: block;
    width: 12px;
    height: 12px;
    float: left;
    border-bottom: 2px solid #00ffff;
    border-right: 2px solid #00ffff;
    position: absolute;
    bottom: -1px;
    right: -1px; }
  .ranking .corner-bottom-left {
    display: block;
    width: 12px;
    height: 12px;
    float: left;
    border-bottom: 2px solid #00ffff;
    border-left: 2px solid #00ffff;
    position: absolute;
    bottom: -1px;
    left: -1px; }
